<template>
    <div class="specific-promo-page">
        <div class="specific-promo-page__text">
            {{text}}
        </div>
        <div class="specific-promo-page__controls">
             <md-button class="md-raised" @click.native="goToPromoAbout">
                 {{constants.learnMore}}
             </md-button>
            <promo-activate-btn></promo-activate-btn>
        </div>
    </div>
</template>

<script>
import { PromoActivateBtn } from '../../promo-activate-btn'
import { mapGetters } from 'vuex'

export default {
  name: 'promo-calc',
  components: {
    PromoActivateBtn
  },
  data () {
    return {
      promoText: {
        ru:
          '–§—É–Ω–∫—Ü–∏—è "–ö–∞–ª—å–∫—É–ª—è—Ç–æ—Ä –ª—É–Ω–Ω—ã—Ö –¥–Ω–µ–π" –¥–æ—Å—Ç—É–ø–Ω–∞ —Ç–æ–ª—å–∫–æ –≤ –ü–µ—Ä—Å–æ–Ω–∞–ª—å–Ω–æ–º –õ—É–Ω–Ω–æ–º –ö–∞–ª–µ–Ω–¥–∞—Ä–µ',
        en:
          '"Lunar days calculator" option is available only in the Personal Lunar Calendar'
      }
    }
  },
  computed: {
    ...mapGetters(['constants']),
    text () {
      return this.promoText[this.$store.state.locale]
    }
  },
  methods: {
    goToPromoAbout () {
      this.$router.push({name: 'promo-page'})
    }
  }
}
</script>

<style lang="scss" scoped>
$padding: 20px;
$top-margin: 15%;

.specific-promo-page {
  margin-top: $top-margin;
  width: 100%;
  height: auto;
  padding: $padding;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 20px;
  &__controls {
      margin: 20px;
  }
}
</style>



